/* START Фоновые изображение */

/* Фоновые изображения для viewport height < 800px */

@media all and (--mediumHeight) {

  .garbage-chute {
    background-image: url('../img/cleaning-medium@1x.jpg');
  }

}

/* и они же для ретины */

@media all and (--mediumHeight) and (-webkit-min-device-pixel-ratio: 1.5) and (min-resolution: 144dpi) and (min-resolution: 1.5dppx) {

  .garbage-chute {
    background-image: url('../img/cleaning-medium@2x.jpg');
  }

}

/* Фоновые изображения для viewport height > 800px */

@media all and (--largeHeight) {

  .garbage-chute {
    background-image: url('../img/cleaning-large@1x.jpg');
  }

}

/* и они же для ретины */

@media all and (--largeHeight) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) and (min-resolution: 2dppx) {

  .garbage-chute {
    background-image: url('../img/cleaning-large@2x.jpg');
  }

}

/* END Фоновые изображение */

/* START Первый экран */

.about-garbage-chute,
.about-garbage-chute-video,
.about-garbage-reviews,
.about-garbage-chute-toOrder,
.about-garbage-chute-features,
.about-garbage-chute-order {
  position: relative;
  left: 7%;
  width: 86%;
  max-width: 86%;
}

.garbage-chute {
  flex-wrap: wrap;
}

.about-garbage-chute {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  width: 800px;
}

.about-garbage-chute> h2 {
  color: var(--textGreen);
}

.about-garbage-chute> h3,
.about-garbage-chute> p {
  color: var(--textGray);
}

.about-garbage-chute>h2 {
  margin-top: 0;
  margin-bottom: 1.5rem;
}

.about-garbage-chute>h3 {
  margin-top: 0;
  margin-bottom: 1rem;
}

.about-garbage-chute>h3 {
  margin-top: 0;
  margin-bottom: 0;
}

.about-garbage-chute-features {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  width: 86%;
  max-width: 86%;
  margin-top: 3vw;
  margin-bottom: 3vw;
}

.about-garbage-chute-feature {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  flex-wrap: nowrap;
  color: var(--textGray);
  font-family: var(--semibold);
  font-size: 1rem;
  margin-right: 1rem;
  margin-left: 1rem;
  flex-basis: 22.5%;
}

.about-garbage-chute-feature> img {
  margin-right: 1.5rem;
}

.about-garbage-chute-feature> p {
  margin: 0;
}

.about-garbage-chute-toOrder {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  flex-wrap: nowrap;
  width: 510px;
  max-width: 86%;
  background-color: rgba(255, 255, 255, 0.6);
  padding: 1.5rem;
}

.about-garbage-chute-toOrder-price {
  flex-basis: 75%;
}

.about-garbage-chute-toOrder> button {
  flex-basis: 20%;
  @apply --customButton;
}

.about-garbage-chute-toOrder> button:hover {
  background-color: color(#c5c5c5 lightness(98%));
}

.about-garbage-chute-toOrder> button:active {
  background-color: white;
}

.about-garbage-chute-toOrder h4,
.about-garbage-chute-order-price h4 {
  color: var(--textGray);
  margin: 0;
}

.about-garbage-chute-toOrder h4> span,
.about-garbage-chute-order-price h4> span,
.about-garbage-chute-order-price> span,
.about-garbage-chute-toOrder> .about-garbage-chute-toOrder-price> span {
  color: var(--textGreen);
}


/* END Первый экран */

/* START Второй экран */

.about-garbage-chute-video {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  align-content: center;
}

.about-garbage-chute-video-description {
  flex-basis: 50%;
  width: 50%;
  max-width: 650px;
  color: var(--textGray);
}

.about-garbage-chute-video-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  flex-basis: 40%;
  width: 40%;
}

.about-garbage-chute-video-wrapper iframe {
  width: 100%;
  height: 100%;
}

.about-garbage-chute-video-1 {
  width: 100%;
  flex-basis: 100%;
  height: 16vw;
  margin-bottom: 1vw;
}

.about-garbage-chute-video-2 {
  width: 48%;
  flex-basis: 48%;
  height: 10vw;
}

.about-garbage-reviews {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  color: var(--textGray);
}

.about-garbage-reviews> button {
  border: 0;
  background-color: transparent;
  margin-top: 4rem;
  text-decoration: underline;
  text-decoration-style: solid;
  color: var(--textGray);
  cursor: pointer;
  outline: none;
}

.about-garbage-reviews> button:hover {
  text-decoration: none;
}

.about-garbage-reviews> h3 {
  color: var(--textGreen);
  margin-top: 0;
  margin-bottom: 4rem;
}

.review-blocks {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  align-content: flex-start;
  overflow: auto;
}

.review-block {
  flex-basis: 45%;
  width: 45%;
  background-color: rgba(255, 255, 255, 0.75);
  padding-top: 3rem;
  padding-bottom: 3rem;
  padding-right: 3.5rem;
  padding-left: 3.5rem;
}

.review-block:first-child {
  margin-right: 7.5%;
}

.review-block> p {
  margin-top: 0;
  margin-bottom: 0;
}

.review-block-author {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  margin-top: 2rem;
}

.review-block-author> p {
  margin-top: 0;
  margin-bottom: 0;
}

.review-block-author> span {
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  color: var(--textGreen);
}

.review-block-author> p:first-child {
  color: var(--textGray);
}

.review-block-author> p:last-child {
  color: #c5c5c5;
}

.about-garbage-chute-order {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  align-content: flex-start;
  color: var(--textGray);
}

.about-garbage-chute-order-makeOrder,
.about-garbage-chute-order-information {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  flex-basis: 48%;
  width: 48%;
}

.about-garbage-chute-order-makeOrder> h3 {
  color: var(--textGreen);
}

.about-garbage-chute-order-makeOrder> form {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 450px;
  max-width: 90%;
}

#garbageChute-numberOfFloors,
#garbageChute-date {
  flex-basis: 42%;
  width: 42%;
  margin-bottom: 0.875rem;
  border: 1px;
  border-style: solid;
  border-radius: 3px;
  color: #c5c5c5;
  padding: 0.5rem;
  height: 1.75rem;
}

#garbageChute-adress {
  flex-basis: 100%;
  width: 100%;
  height: 7rem;
  margin-bottom: 0.875rem;
  border: 1px;
  border-style: solid;
  border-radius: 3px;
  color: #c5c5c5;
  overflow: auto;
  padding: 0.5rem;
}

#garbageChute-makeOrder {
  @apply (--customButton);
}

.about-garbage-chute-order-price {
  margin-top: 4rem;
}

.about-garbage-chute-order-information-adresses {
  background-color: color(white alpha(70%));
  padding: 2rem;
}

.about-garbage-chute-order-information-adresses> h4 {
  margin: 0;
}

.about-garbage-chute-order-information-adresses> .divider {
  position: relative;
  left: -2rem;
  height: 1px;
  width: 100%;
  background-color: color(#c5c5c5 alpha(70%));
  margin-top: 1rem;
  padding-left: 2rem;
  padding-right: 2rem;
}

.about-garbage-chute-order-information-adresses> .adresses {
  height: 15rem;
  overflow: auto;
}

.about-garbage-chute-order-information-appeal> button {
  background-color: transparent;
  border: 0;
  text-decoration: underline;
  text-decoration-style: solid;
  cursor: pointer;
  color: var(--textGray);
  margin-top: 2rem;
  margin-bottom: 2rem;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
}

.about-garbage-chute-order-information-appeal> button> svg {
  margin-right: 1rem;
}

.about-garbage-chute-order-information-appeal> button:hover {
  text-decoration: none;
}

.about-garbage-chute-order-soc-buttons {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
}

.about-garbage-chute-order-soc-buttons> a {
  margin-left: 1rem;
  margin-right: 1rem;
}

.about-garbage-chute-order-soc-buttons> a:first-child {
  margin-left: 0;
}

.about-garbage-chute-order-soc-buttons> a:last-child {
  margin-right: 0;
}

#instaShare,
#fbShare,
#vkShare {
  transition-property: fill;
  transition-duration: 0.2s;
  transition-timing-function: ease;
}

.about-garbage-chute-order-soc-buttons a:hover #instaShare,
.about-garbage-chute-order-soc-buttons a:hover #fbShare,
.about-garbage-chute-order-soc-buttons a:hover #vkShare {
  fill: #c5c5c5;
}
