.vacancies-group-select> h2 {
  margin-right: 3rem;
  color: var(--textGray);
}

@media (--mobile) {
  .vacancies> h2 {
    margin-top: 0;
  }
}

.vacancies-group-select> span {
  margin-left: 0.75rem;
  margin-right: 0.75rem;
  color: var(--textGreen);
}

/* Блок с кнопками для навигации по вакансиям */
.vacancies-select,
.vacancies-select> nav {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  align-content: flex-start;
  flex-wrap: nowrap;
  overflow: auto;
}

.vacancies-select {
  flex-basis: 40%;
  width: 40%;
  justify-content: space-between;
  height: 55vh;
}

.contacts-mobile {
  display: none;
}

.contacts-desktop {
  margin-bottom: 4rem;
}

.contacts-desktop> a {
  display: inline-block;
}

@media (--small) {
  .contacts-desktop {
    font-size: 14px !important;
    margin-bottom: 2rem;
  }
}

@media (--mobile) {
  .vacancies-select {
    flex-basis: 100%;
    width: 100%;
    height: auto;
  }
  .contacts-desktop {
    display: none;
  }
  .contacts-mobile {
    display: block;
    margin-bottom: 2rem;
  }
  .contacts-mobile> a {
    color: var(--textGray);
    display: inline-block;
  }
  .contacts-mobile> a:visited {
    color: var(--textGray);
  }
}

.vacancies-select> nav {
  justify-content: flex-start;
}

.vacancies-select> nav {
}

.vacancies-select> p {
  width: 70%;
  color: var(--textGray);
}

.vacancies-select> p> a,
.vacancies-select> p> a:visited {
  color: var(--textGray);
  cursor: default;
}

/* Кнопки для навигации по вакансиям */

.vacancies-select> nav> button {
  @apply --controls;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  text-align: right;
  margin-left: 0.8em;
}

.vacancies-select> nav> button:disabled::before { /* Стрелка для стилизации */
  content: url('../img/selectArrowSmall.svg');
  margin-right: 0.5em;
}

.vacancies-select> nav> button:hover {
  color: var(--textGray);
}

.vacancies-select> nav> button:disabled {
  color: var(--textGreen);
  cursor: default;
  margin-left: 0em;
}

/* Блок с описанием выбранной вакансии */

.vacancie-description {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  flex-basis: 40%;
  padding: 2.5rem;
  background-color: color(white alpha(60%));
  max-height: 40vh;
  overflow-y: auto;
}

@media (--mobile) {
  .vacancie-description {
    margin-top: 2rem;
    flex-basis: 100%;
    width: 100%;
    padding: 0;
    max-height: 100%;
    height: auto;
  }
}

.vacancie-description> h3,
.vacancie-description> p {
  color: var(--textGray);
}

.vacancie-description> h3 {
  margin-top: 0;
  margin-left: 0;
  margin-right: 0;
  margin-bottom: 1rem;
}

.vacancie-description> p {
  margin: 0;
}

.vacancies-group-select> button {
  @apply --controls;
  padding: 0;
  margin: 0;
}

.vacancies-group-select> button:hover {
  color: var(--textGray);
}

.vacancies-group-select> button:disabled {
  color: var(--textGreen);
  cursor: default;
}

@media (--small) {
  .vacancies-select {
    flex-basis: 35%;
    width: 35%;
  }
  .vacancie-description {
    flex-basis: 50%;
    width: 50%;
  }
}

@media (--medium) {
  .vacancie-description {
    flex-basis: 50%;
    width: 50%;
  }
}
