@import 'variables.css';
@import 'normalize.css';
@import 'fontfaces.css';
@import 'homepage.css';
@import "cleaningAndPest.css";
@import "garbageChute.css";
@import "contacts.css";
@import "news.css";
@import "vacancies.css";
@import "lightgallery.css";
@import "modals.css";
@import "owl.carousel.min.css";

body { font-family: sans-serif; }

.fonts-loaded body {
  font-family: 'Lato Regular', sans-serif;
}

/* START Конфигурации типографики */

.fonts-loaded .light16 {
  font-family: var(--light);
  font-size: 1rem;
  line-height: 1.2rem;
}

.fonts-loaded .light20 {
  font-family: var(--light);
  font-size: 1.25rem;
  line-height: 1.2rem;
}

.fonts-loaded .light24 {
  font-family: var(--light);
  font-size: 1.5rem;
  line-height: 2.5rem;
}

.fonts-loaded .light54 {
  font-family: var(--light);
  font-size: 3.375rem;
  line-height: normal;
}

.fonts-loaded .regular12 {
  font-family: var(--regular);
  font-size: 0.75rem;
  line-height: 1.714rem;
}

.fonts-loaded .regular14 {
  font-family: var(--regular);
  font-size: 0.875rem;
  line-height: 1.714rem;
}

.fonts-loaded .regular16 {
  font-family: var(--regular);
  font-size: 1rem;
  line-height: 1.714rem;
}

.fonts-loaded .regular18 {
  font-family: var(--regular);
  font-size: 1.125rem;
  line-height: 1.714rem;
}

.fonts-loaded .regular24 {
  font-family: var(--regular);
  font-size: 1.4rem;
  line-height: 1.714rem;
}

.fonts-loaded .semibold16 {
  font-family: var(--semibold);
  font-size: 1rem;
  line-height: normal;
}

.fonts-loaded .semibold18 {
  font-family: var(--semibold);
  font-size: 1.125rem;
  line-height: normal;
}

.fonts-loaded .semibold14 {
  font-family: var(--semibold);
  font-size: 0.875rem;
  line-height: normal;
}

.fonts-loaded .bold14 {
  font-family: var(--bold);
  font-size: 0.875rem;
  line-height: 1.714rem;
}

.fonts-loaded .bold16 {
  font-family: var(--bold);
  font-size: 1rem;
  line-height: 1.714rem;
}

.fonts-loaded .italic14 {
  font-family: var(--italic);
  font-size: 0.875rem;
  line-height: 1.714rem;
}

.fonts-loaded .italic16 {
  font-family: var(--italic);
  font-size: 1rem;
  line-height: 1.714rem;
}

/* END Конфигурации типографики */

/* START Конфигурации ссылок */

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:focus {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
  color: var(--textGray);
}

a:visited .actual {
  color: var(--textGreen);
}

/* Отступы ссылок в header */

header> a {
  margin-right: 1rem;
}

/* END Конфигурации ссылок */

/* START Зафиксированные элементы (лого, навигация, заявка, социальные кнопки, копирайт) */

header,
footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: center;
  align-items: center;
  position: fixed;
  left: 7%;
  width: 86%;
  z-index: 11;
}

header {
  top: 5%;
}

footer {
  bottom: 5%;
}

.hamburger {
  display: none;
  border: 0;
  background-color: transparent;
  cursor: pointer;
  outline: none;
  width: 100%;
  height: 30px;
}

.hamburger-line {
  display: block;
  width: 34px;
  height: 3px;
  background-color: var(--textGreen);
  margin-bottom: 5px;
  margin-left: auto;
  margin-right: auto;
}

.hamburger_hidden,
#openOrderCleaning_insideNav {
  display: none;
}



/* START Header */

.logo {
  width: 140px;
  transition-property: width;
  transition-duration: 0.2s;
  transition-timing-function: ease;
  margin-right: 1rem;
}

.fixed-elements__logo { /* Логотип */
  margin-right: 1rem;
  max-width: 145px;
  max-height: 51px;
  transform: scale(1);
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}

.fixed-elements__logo> svg {
  transform: scale(1);
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}

@media (--small) {
  .fixed-elements__logo> svg,
  .fixed-elements__logo {
    transform: scale(0);
  }
  .logo {
    width: 0;
  }
}

header> nav { /* Панель навигации */
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  margin-right: 1rem;
  background-color: color(white alpha(90%));
  border-radius: 3px;
  padding-top: 0.25em;
  padding-bottom: 0.25em;
}

@media (--small) {
  header> nav {
    margin-left: -2rem;
  }
}

header> nav> a { /* Ссылки на панели навигации */
  margin-left: 1em;
  margin-right: 1em;
  margin-top: 0.25em;
  margin-bottom: 0.25em;
  color: var(--textGray);
  transition-property: color;
  transition-duration: var(--time);
  transition-timing-function: ease;
}

header> nav> a:hover { /* Ховер ссылок на панели навигации */
  color: var(--textGreen);
}

.fixed-elements__submit-an-application { /* Контейнер кнопки "Оставить заявку" */
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  align-content: center;
}

.fixed-elements__submit-an-application> button { /* Кнопка "Оставить заявку" */
  min-width: 11em;
  background-color: var(--textGreen);
  border-radius: 3px;
  border: 0;
  color: white;
  letter-spacing: normal;
  transition-property: background-color, margin-top;
  transition-duration: 0.1s;
  transition-timing-function: ease;
  outline: none;
  cursor: pointer;
  padding-top: 0.8rem;
  padding-bottom: 0.7rem;
}

.fixed-elements__submit-an-application> button:hover {
  background-color: color(var(--textGreen) lightness(55%));
}

.fixed-elements__submit-an-application> button:active {
  background-color: color(var(--textGreen) lightness(60%));
}

@media (--mobile) {
  header {
    position: relative;
    top: 0;
    flex-direction: column;
    justify-content: flex-start;
    left: 0;
    width: 100%;
    height: 22vh;
    background-color: white;
  }
  .mobile-nav_visible {
    position: fixed;
    height: 100%;
  }
  header> .logo {
    height: 22vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    align-content: center;
  }
  .mobile-nav_visible> .logo {
    height: 22vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
  header> nav {
    display: none;
  }
  .mobile-nav_visible> nav {
    display: flex;
    background-color: var(--textGreen);
    border-radius: 0;
    padding: 0;
    margin: 0;
    height: 0%;
    width: 100%;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    align-content: flex-start;
  }
  header> nav> a,
  header> nav .actual,
  #openOrderCleaning_insideNav  {
    color: white !important;
    font-size: 1.5rem;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: 10%;
    margin-right: 0;
    padding: 0;
    cursor: pointer;
  }
  header> nav> a:first-child {
    margin-top: 1rem;
  }
  #openOrderCleaning_insideNav {
    margin-bottom: 1rem;
  }
  .fixed-elements__logo  {
    margin: 0;
  }
  .hamburger,
  #openOrderCleaning_insideNav {
    display: block;
  }
  #openOrderCleaning_insideNav {
    border: 0;
    background-color: transparent;
    color: white;
    font-weight: 600;
  }
  footer,
  #openOrderCleaning {
    display: none;
  }
}

/* END Header */

/* START Footer */

/* Социальные кнопки */

footer> .social-buttons> a,
.social-buttons-mobile> a {
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  margin-top: 0;
  margin-bottom: 0;
}

footer> .social-buttons> a:first-child,
.social-buttons-mobile> a:first-child {
  margin-left: 0rem;
}

footer> .social-buttons> a:last-child,
.social-buttons-mobile> a:last-child  {
  margin-right: 0rem;
}

/* Контейнер копирайта */

footer> .copyright {
  color: #515151;
  opacity: 0.5;
}

/* END Footer */

/* END Зафиксированные элементы (лого, навигация, заявка, социальные кнопки, копирайт) */

/* START .underline */

.underline {
  width: 100%;
  height: 1px;
  margin-top: -4px;
}

.underline-invisible {
  opacity: 0;
  transition-property: opacity;
  transition-duration: var(--time);
  transition-timing-function: ease;
}

header> nav> a:hover> .underline-invisible {
  opacity: 1;
}

.underline-visible {
  opacity: 1;
}

.underline-gray {
  background-color: var(--textGray);
  opacity: 0.3;
}

.underline-green {
  background-color: var(--textGreen);
}



/* END .underline */

/* START .fixed-pagination */

.fixed-pagination {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  position: fixed;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  width: 50px;
  border-radius: 3px 0px 0px 3px;
  background-color: white;
}

@media (--mobile) {
  .fixed-pagination {
    display: none;
  }
}

.fixed-pagination__link {
  width: 0.7rem;
  height: 0.7rem;
  margin-top: 0.425rem;
  margin-bottom: 0.425rem;
  margin-right: 0.85rem;
  border-radius: 0.35rem;
  background-color: color(var(--textGray) alpha(20%));
  transition-property: background-color;
  transition-duration: 0.25s;
  transition-timing-function: ease;
}

.fixed-pagination__link:first-child {
  margin-top: 0.85rem;
}

.fixed-pagination__link:last-child {
  margin-bottom: 0.85rem;
}

.fixed-pagination__link_active {
  background-color: var(--textGray);
  transition-property: background-color;
  transition-duration: 0.25s;
  transition-timing-function: ease;
}

/* END .fixed-pagination */

.social-buttons> a> svg #insta,
.social-buttons> a> svg #fb,
.social-buttons> a> svg #vk {
  transition-property: fill;
  transition-duration: var(--time);
  transition-timing-function: ease;
}

.social-buttons> a:hover> svg #insta,
.social-buttons> a:hover> svg #fb,
.social-buttons> a:hover> svg #vk {
  fill: var(--textGray);
}

.actual {
  color: var(--textGreen) !important;
}
