/* Модальное окно */

.modal {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  visibility: visible;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  background-color: white;
  z-index: 1010;
  transition-property: transform, opacity;
  transition-duration: 0.25s;
  transition-timing-function: ease-out;
  border: 1px;
  border-radius: 2px;
  border-color: #eeeeee;
  border-style: solid;
  overflow-y: auto;
  overflow-x: hidden;
}

/* Модальное окно скрыто */

.modal-closed {
  visibility: hidden;
}

/* Подложка */

.modal-overlay {
  z-index: 1000;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: color(white alpha(90%))
}

/* Кнопка закрытия модального окна */

.closeModal {
  width: 23px;
  height: 23px;
  opacity: 0.2;
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  transition-property: opacity;
  transition-duration: 0.2s;
  transition-timing-function: ease;
}

.closeModal:hover {
  opacity: 0.5;
}

/* Анимация появления */

.modal-show-animation {
  opacity: 1;
  transition-property: opacity;
  transition-duration: 0.2s;
  transition-timing-function: ease;
}

/* Анимация скрытия */

.modal-hide-animation {
  opacity: 0;
  transition-property: opacity;
  transition-duration: 0.2s;
  transition-timing-function: ease;
}

::placeholder {
  font-family: var(--regular);
  font-size: 0.875rem;
  color: color(var(--textGray) lightness(75%));
}

.input-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  width: 100%;
}

/* Размеры модальных окон */

#orderCleaning,
#giveFeedback,
#onetime,
#afterConstr,
#subscription,
#pestControl {
  max-width: 100%;
  max-height: 100%;
}

#orderCleaning,
#giveFeedback {
  width: 444px;
  height: auto;
}

#onetime {
  width: 1010px;
  height: auto;
}

#afterConstr {
  width: 550px;
  height: auto;
}

#pestControl {
  width: 820px;
  height: auto;
}

/* Размеры формы */

#orderCleaning> form,
#giveFeedback> form,
#afterConstr> form,
#onetime> form,
#pestControl> form {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  flex-wrap: wrap;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
}

#orderCleaning> form,
#giveFeedback> form {
  width: 72%;
}

#afterConstr> form {
  width: 80%;
}

#onetime> form,
#pestControl> form {
  width: 90%;
}

/* Стилизация поля формы */

#orderCleaning> form input,
#giveFeedback> form input,
#giveFeedback> form textarea,
#afterConstr> form input,
#onetime> form input,
#pestControl> form input {
  border: 1px;
  border-radius: 2px;
  border-color: var(--textGray);
  border-style: solid;
  font-family: var(--regular);
  font-size: 1rem;
  color: var(--textGray);
  padding: 0.5rem;
  margin-bottom: 1rem;
  outline: none;
  transition-property: broder-color;
  transition-duration: 0.2s;
  transition-timing-function: ease;
}

#orderCleaning> form button,
#giveFeedback> form button,
#afterConstr> form button,
#onetimeSubmitOrder,
#pestControlSubmitOrder {
  @apply (--customButton);
  transition-property: background-color;
  transition-duration: 0.2s;
  transition-timing-function: ease;
}

#orderCleaning> form button,
#giveFeedback> form button {
  margin-top: 1rem;
  margin-bottom: 2rem;
}

#orderCleaning> form button:hover,
#giveFeedback> form button:hover,
#afterConstr> form button:hover {
  background-color: color(black alpha(2%));
}

#orderCleaning> form button:active,
#giveFeedback> form button:active,
#afterConstr> form button:active {
  background-color: white;
}

/* Заголовки */

#orderCleaning> form h2,
#afterConstr> form h2,
#giveFeedback> form h2,
#onetime> h2,
#pestControl> h2 {
  width: 100%;
  color: var(--textGray);
  margin-top: 2rem;
}

#onetime> h2,
#pestControl> h2 {
  margin-left: 5%;
}

/* Размеры и позиции элементов */

#orderCleaning> form input {
  width: 100%;
}

#afterConstr #complexity {
  width: 100%;
  height: 1.6rem;
}

#afterConstr> form> .input-row> input {
  width: 42%;
}

#afterConstr .input-row-half {
  margin-right: 3rem;
}

#afterConstr .input-row-half> label {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
}

#afterConstr .input-row-half> label> div {
  margin-left: 2rem;
}

.row-element {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-content: flex-end;
  align-items: flex-end;
  align-items: baseline;
}

.row-element p,
.row-element button {
  margin-bottom: 0;
  margin-top: 0;
}

.margin-row {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.margin-row> input {
  margin-bottom: 0 !important;
}

.discount {
  padding-left: 0.5em;
  padding-right: 0.5em;
  padding-top: 0.1em;
  background-color: color(#c5c5c5 alpha(20%));
  border-radius: 3px;
  color: var(--textGray);
}

#giveFeedback> form textarea {
  width: 100%;
  height: 80px;
  overflow-y: auto;
}

#onetime> form {
  flex-wrap: nowrap;
}

#modalCol1 {
  width: 40%;
  margin-right: 5%;
}

#modalCol1> label,
#modalCol2> label {
  display: block;
  color: var(--textGray);
}

#pestControl label {
  margin-top: 0.75rem;
  margin-bottom: 0.75rem;
}

#pestControl h3 {
  color: var(--textGray);
}

#pestControl #modalCol2 h3 {
  margin: 0;
}

#modalCol1> label> div,
#modalCol2> label> div {
  float: right;
}

#modalCol1 input {
  width: 40%;
}

#onetime #modalCol1 h2 {
  color: var(--textGreen);
}

#modalCol2 {
  width: 55%;
}
#modalCol2 .cleaning-description-service__description {
  font-size: 14px;
  line-height: 24px;
}

#onetime #modalCol2 label {
  display: inline-block;
  float: right;
}

#onetimePriceInfo {
  position: absolute;
  bottom: 0;
  width: 50%;
}

/* Выбор дом/квартира */

.placeSelect {
  display: inline-block;
  margin-top: 0;
  margin-bottom: 1rem;
  margin-left: 0;
  margin-right: 0;
  padding: 0;
  width: 42%;
  border: 0px;
}

.placeSelect> span {
  margin-left: 1rem;
  margin-right: 1rem;
}

.placeSelect span,
.placeSelect label {
  display: inline-block;
  vertical-align: middle;
  height: 2em;
  margin-top: 0.75em;
}

.placeSelect label {
  cursor: pointer;
}

input[type="radio"],
input[type="checkbox"] {
  display:none;
}

input[type="radio"] + label {
  cursor: pointer;
  color: #c5c5c5;
}

input[type="radio"]:checked {
  cursor: default;
}

input[type="radio"]:checked + label {
  color: var(--textGray);
  font-weight: 600;
  cursor: default;
}

input[type="checkbox"] + label {
  cursor: pointer;
  color: var(--textGray);
  position: relative;
}

input[type="radio"] + label> .customCheckbox> span,
input[type="checkbox"] + label> .customCheckbox> span {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 5px;
  padding: 0;
  position: absolute;
  top: 5px;
  left: 5px;
  transition-property: transform, opacity;
  transition-duration: 0.1s;
  transition-timing-function: ease;
}

input[type="radio"]:checked + label> .customCheckbox> span,
input[type="checkbox"]:checked + label> .customCheckbox> span {
  background-color: var(--textGreen);
  position: absolute;
  top: 5px;
  left: 5px;
}

.customCheckbox {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 1px;
  border-color: var(--textGreen);
  border-style: solid;
  border-radius: 3px;
}

@media (--mobile) {
  .closeModal {
    transform: scale(1.5);
    position: absolute;
    top: 2.575rem;
    right: 21px;
  }
  #onetime {
    overflow-y: auto;
    overflow-x: hidden;
  }
  #onetime> form {
    flex-wrap: wrap;
    width: 90%;
  }
  #modalCol1,
  #modalCol2 {
    width: 100%;
    margin-right: 0;
  }
  .placeSelect > span {
    margin-left: 0.5rem;
    margin-right: 0.2rem;
  }
  #onetimePriceInfo {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
  #onetimeSubmitOrder,
  #afterConstrSubmitOrder,
  #orderCleaning button,
  #pestControlSubmitOrder {
    width: 60%;
    justify-content: center;
    color: white !important;
    background-color: var(--textGreen) !important;
    margin-top: 2rem;
    margin-left: auto;
    margin-right: auto;
  }
  .input-row-half {
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
  }
  .input-row .margin-row {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
  .cleaning-order> button {
    font-size: 0.8rem !important;
  }
  #orderCleaning h2 {
    margin-bottom: 2rem;
  }
  #facadeCalculatePrice {
    flex-direction: column;
  }
  .facadePrice {
    height: 3rem;
  }
}

@media (--small) {
  #pestControlOrder> #modalCol1 {
    width: 35%;
  }
  #pestControlOrder> #modalCol2 {
    width: 60%;
  }
  #onetimePriceInfo {
    flex-direction: row;
    align-items: center;
    align-content: center;
    justify-content: space-around;
  }
  #pestControlSubmitOrder {
    margin-top: 1rem;
    margin-bottom: 0.5rem;
  }
  .cleaning-order {
    flex-direction: column;
    align-items: flex-end;
  }
  .cleaning-order a {
    margin-right: 0;
    margin-bottom: 1rem;
  }
  #onetimeSubmitOrder {
    margin-top: 0.5rem;
    width: 60%;
    justify-content: center;
  }
  .placeSelect {
    font-size: 12px;
  }
  .placeSelect> span {
    margin-left: 0.7rem;
    margin-right: 0.5rem;
  }
}
