#news {
  flex-wrap: wrap;
  position: relative;
  top: 35vh;
}

@media (--small) {
  #news {
    top: 32vh;
  }
}

@media (--mobile) {
  #news {
    top: 2vh;
  }
}

.header {
  width: 100%;
  height: 35vh;
  background-color: white;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
}

@media (--small) {
  .header {
    height: 32vh;
  }
}

.newsfeed {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  align-content: flex-start;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 86%;
  position: absolute;
  top: 0;
  left: 7%;
}

@media (--mobile) {
  .newsfeed {
    width: 94%;
    left: 3%;
    position: relative;
    top: 35vh;
  }
}

.newsfeed> a {
  color: var(--textGray);
}

.news-nav {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  align-content: flex-start;
  justify-content: flex-start;
  width: 86%;
  position: fixed;
  left: 7%;
  top: 20%;
}

@media (--mobile) {
  .news-nav {
    position: relative;
    top: auto;
  }
}

.newsfeed-article-link {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  width: 22%;
  padding: 0.4rem;
  border: 1px;
  border-style: solid;
  border-radius: 3px;
  border-color: #c5c5c5;
  margin-bottom: 4rem;
  margin-left: 4%;
  margin-right: 4%;
  transition-property: border-color;
  transition-duration: var(--time);
  transition-timing-function: ease;
}

@media (--mobile) {
  .newsfeed-article-link {
    width: 100%;
    margin-bottom: 1rem;
  }
}

@media (--small) {
  .newsfeed-article-link {
    margin-left: 1%;
    margin-right: 1%;
    width: 28%;
  }
}

@media (--medium) {
  .newsfeed-article-link {
    margin-left: 3%;
    margin-right: 3%;
    width: 24%;
  }
}

@media (--large) {
  .newsfeed-article-link {
    margin-left: 3%;
    margin-right: 3%;
    width: 23%;
  }
}

.newsfeed-article-link:hover {
  border-color: var(--textGreen);
}



.newsfeed-article-link> img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.newsfeed-article-link:hover> img {
  filter: contrast(110%);
}

.article-link__heading {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 1rem;
  margin-bottom: 0.4rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

article-link__img {
}

.news-nav> a {
  @apply --controls;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  text-align: right;
  font-size: 24px;
}

.news-nav> a:visited {
  color: #c5c5c5;
}

.news-nav> a:visited:hover,
.news-nav> a:hover {
  color: var(--textGray);
}

.header> nav> a> .actual {
  color: var(--textGreen);
}

.news-nav> a::before { /* Стрелка для стилизации */
  content: url('../img/selectArrow.svg');
  margin-right: 0.8em;
}

.news-nav> .back::before { /* Стрелка для стилизации */
  content: url('../img/back.svg');
  margin-right: 0.8em;
}

#article {
  position: relative;
  top: 35vh;
  height: auto;
  align-items: flex-start;
  align-content: flex-start;
}

@media (--mobile) {
  #article,
  #article> article {
    flex-direction: column !important;
  }
  #article {
    top: 2vh;
  }
  #article> article {
    width: 100% !important;
    left: 0 !important;
  }
}

@media (--small) {
  #article,
  #article> article {
    flex-direction: column !important;
  }
  #article {
    top: 35vh !important;
  }
  #article> article {
    width: 100% !important;
    left: 0 !important;
  }
}

@media (--medium) {
  #article,
  #article> article {
    flex-direction: column !important;
  }
  #article {
    top: 35vh !important;
  }
  #article> article {
    width: 100% !important;
    left: 0 !important;
  }
}

#article> article {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  width: 86%;
  position: relative;
  left: 7%;
}

.article-text-heading {
  color: var(--textGreen);
  margin-top: 0;
  margin-bottom: 1rem;
}

.article-text-subheading {
  color: var(--textGray);
  margin-top: 0;
  margin-bottom: 2rem;
}

.article-text-text {
  color: var(--textGray);
  margin-top: 0.5rem;
}

.article-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-basis: 50%;
  width: 50%;
  margin-right: 10%;
}

@media (--mobile) {
  .article-wrapper {
    flex-basis: 86%;
    width: 86%;
    margin-left: 7%;
    top: 35vh;
    position: relative;
    margin-bottom: 35vh;
  }
}

@media (--small) {
  .article-wrapper {
    flex-basis: 86%;
    width: 86%;
    margin-left: 7%;
  }
}

@media (--medium) {
  .article-wrapper {
    flex-basis: 86%;
    width: 86%;
    margin-left: 7%;
  }
}

.slider-wrapper {
  position: fixed;
  top: 35vh;
  left: 60%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-basis: 40%;
  width: 400px;
  height: 400px;
  max-width: 400px;
}

.siema-article {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-basis: 40%;
  max-width: 400px;
  position: fixed;
  top: 35vh;
  left: 60%;
}

.siema-article-item {
  width: 100%;
  height: auto;
  object-fit: cover;
}

@media (--mobile) {
  .slider-wrapper {
    position: relative;
    width: 100%;
    max-width: 100%;
    flex-basis: 100%;
    margin: 0;
    top: auto;
    left: auto;
  }
  .siema-article {
    position: relative;
    width: 100%;
    max-width: 100%;
    flex-basis: 100%;
    top: auto;
    left: auto;
  }
}

@media (--small) {
  .slider-wrapper {
    position: relative;
    width: 100%;
    max-width: 540px;
    flex-basis: 100%;
    height: 100%;
    margin-top: 2rem;
    margin-bottom: 2rem;
    top: auto;
    left: auto;
    margin-left: auto;
    margin-right: auto;
  }
  .siema-article {
    position: relative;
    width: 100%;
    max-width: 100%;
    flex-basis: 100%;
    height: 100%;
    top: auto;
    left: auto;
  }
}

@media (--medium) {
  .slider-wrapper {
    position: relative;
    width: 100%;
    max-width: 540px;
    flex-basis: 100%;
    height: 100%;
    margin-top: 2rem;
    margin-bottom: 2rem;
    top: auto;
    left: auto;
    margin-left: auto;
    margin-right: auto;
  }
  .siema-article {
    position: relative;
    width: 100%;
    max-width: 100%;
    flex-basis: 100%;
    height: 100%;
    top: auto;
    left: auto;
  }
}

.siema-article-prev {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-content: center;
  position: relative;
  top: 50%;
  left: 0;
  width: 50px;
  height: 50px;
  background-color: color(white alpha(95%));
  z-index: 40;
  border-radius: 25px;
  border: 1px;
  border-style: solid;
  border-color: #dbdbdb;
  transform: translateY(-25px);
  margin-left: -25px;
  cursor: pointer;
  outline: none;
}

.siema-article-next {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-content: center;
  position: relative;
  top: 50%;
  left: 100%;
  width: 50px;
  height: 50px;
  background-color: color(white alpha(95%));
  z-index: 40;
  border-radius: 25px;
  border: 1px;
  border-style: solid;
  border-color: #dbdbdb;
  transform: translateY(-75px);
  margin-left: -25px;
  cursor: pointer;
  outline: none;
}

@media (--mobile) {
  .siema-article-prev {
    display: none;
    position: absolute;
    top: 50%;
    left: 10px;
    right: auto;
    transform: translateY(-50%);
    margin: 0;
  }
  .siema-article-next {
    display: none;
    position: absolute;
    top: 50%;
    left: auto;
    right: 10px;
    transform: translateY(-50%);
    margin: 0;
  }
}

@media (--small) {
  .siema-article-prev {
    position: absolute;
    top: 50%;
    left: -25px;
    transform: translateY(-50%);
    margin: 0;
  }
  .siema-article-next {
    position: absolute;
    top: 50%;
    left: 515px;
    transform: translateY(-50%);
    margin: 0;
  }
}

@media (--medium) {
  .siema-article-prev {
    position: absolute;
    top: 50%;
    left: -25px;
    transform: translateY(-50%);
    margin: 0;
  }
  .siema-article-next {
    position: absolute;
    top: 50%;
    left: 515px;
    transform: translateY(-50%);
    margin: 0;
  }
}
