/* Сменяющиеся слайды главной страницы */

/* Обёртка экранов главной страницы */

.homepage-screens-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  width: 100%;
  overflow: hidden;
}

/* Контейнер экрана главной страницы */

.homepage-screen {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  width: 100%;
  height: 100vh;
}

@media (--mobile) {
  .homepage-screen {
    height: auto;
  }
}

/* Стили для правильного отображения фоновых изображений */

.homepage-screens-background {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 100% 90%;
}

/* START Фоновые изображение */

/* Фоновые изображения для viewport height < 800px */

@media all and (--mediumHeight) {

  #homepage-screen-1 {
    background-image: url('../img/homepage1-medium@1x.jpg');
  }

  #homepage-screen-2 {
    background-image: url('../img/homepage1-medium@1x.jpg');
  }

  #homepage-screen-3 {
    background-image: url('../img/homepage1-medium@1x.jpg');
  }

  #homepage-screen-4 {
    background-image: url('../img/homepage1-medium@1x.jpg');
  }

}

/* и они же для ретины */

@media all and (--mediumHeight) and (-webkit-min-device-pixel-ratio: 1.5) and (min-resolution: 144dpi) and (min-resolution: 1.5dppx) {

  #homepage-screen-1 {
    background-image: url('../img/homepage1-medium@2x.jpg');
  }

  #homepage-screen-2 {
    background-image: url('../img/homepage1-medium@2x.jpg');
  }

  #homepage-screen-3 {
    background-image: url('../img/homepage1-medium@2x.jpg');
  }

  #homepage-screen-4 {
    background-image: url('../img/homepage1-medium@2x.jpg');
  }

}

/* Фоновые изображения для viewport height > 800px */

@media all and (--largeHeight) {

  #homepage-screen-1 {
    background-image: url('../img/homepage1-large@1x.jpg');
  }

  #homepage-screen-2 {
    background-image: url('../img/homepage1-large@1x.jpg');
  }

  #homepage-screen-3 {
    background-image: url('../img/homepage1-large@1x.jpg');
  }

  #homepage-screen-4 {
    background-image: url('../img/homepage1-large@1x.jpg');
  }

}

/* и они же для ретины */

@media all and (--largeHeight) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) and (min-resolution: 2dppx) {

  #homepage-screen-1 {
    background-image: url('../img/homepage1-large@2x.jpg');
  }

  #homepage-screen-2 {
    background-image: url('../img/homepage1-large@2x.jpg');
  }

  #homepage-screen-3 {
    background-image: url('../img/homepage1-large@2x.jpg');
  }

  #homepage-screen-4 {
    background-image: url('../img/homepage1-large@2x.jpg');
  }

}

/* END Фоновые изображение */

/* START Базовые стили блока с информацией */

.homepage-screens-info-wrapper {
  position: relative;
  left: 7%;
  max-width: 79%;
  z-index: 2;
  background-color: color(white alpha(95%));
  padding: 0.3rem;
  border-radius: 4px;
}

.homepage-screens-info {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  border: 1px;
  border-color: color(var(--textGreen) alpha(35%));
  border-style: solid;
  border-radius: 3px;
}

/* END Базовые стили блока с информацией */

/* START #homepage-screen-1 */

#homepage-screen-1> .homepage-screens-info-wrapper { /* Обёртка слайдера */
  width: 650px;
}

.homepage-screens-info__item { /* Слайд */
  padding-top: 1em;
  padding-bottom: 1em;
  padding-left: 1em;
  padding-right: 1em;
  color: var(--textGray);
}

#homepage-screen-1 .homepage-screens-info> button { /* Кнопки слайдера */
  background-color: transparent;
  border: 0;
  cursor: pointer;
  color: color(var(--textGray) alpha(45%));
  outline: none;
}

.homepage-screens-info> .prev-screen1 { /* Нопка назад */
  padding-left: 1em;
}

.homepage-screens-info> .next-screen1 { /* Нопка вперёд */
  padding-right: 1em;
}

/* END #homepage-screen-1 */

/* START #homepage-screen-2 */

#homepage-screen-2> .homepage-screens-info-wrapper {
  width: 600px;
}

#homepage-screen-2 .homepage-screens-info {
  flex-wrap: wrap;
}

#homepage-screen-2 h1 {
  color: var(--textGreen);
}

#homepage-screen-2 p,
#homepage-screen-2 h3 {
  color: var(--textGray);
}

#homepage-screen-2 p,
#homepage-screen-2 h1,
#homepage-screen-2 h3,
#homepage-screen-2 button {
  margin-left: 2.5rem;
  margin-right: 2.5rem;
  margin-top: 0rem;
  margin-bottom: 0rem;
}

#homepage-screen-2 .homepage-screens-info> h1 {
  margin-top: 2rem;
  margin-bottom: 0.8rem;
}

#homepage-screen-2 .homepage-screens-info> h3 {
  margin-bottom: 1.5rem;
  line-height: 1.5rem;
}

#homepage-screen-2 .homepage-screens-info> button {
  margin-top: 1.5rem;
  margin-bottom: 2rem;
  color: var(--textGreen);
  background-color: white;
  border: 1px;
  border-color: var(--textGreen);
  border-radius: 3px;
  border-style: solid;
  padding-top: 0.5em;
  padding-bottom: 0.4em;
  padding-left: 1.5em;
  padding-right: 1.5em;
  cursor: pointer;
  outline: none;
  transition-property: background-color;
  transition-duration: var(--time);
  transition-timing-function: ease;
}

#homepage-screen-2 .homepage-screens-info> button:hover {
  background-color: color(black alpha(2%));
}

#homepage-screen-2 .homepage-screens-info> button:active {
  background-color: color(black alpha(1%));
}

/* END #homepage-screen-2 */

/* START #homepage-screen-3 */

#homepage-screen-3> .homepage-screens-info-wrapper {
  width: 980px;
}

#homepage-screen-3 .homepage-screens-info {
  flex-wrap: nowrap;
  padding-top: 3rem;
  padding-bottom: 3rem;
  justify-content: space-around;
}

#homepage-screen-3 .homepage-screens-info> .features,
#homepage-screen-3 .homepage-screens-info> .facts {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}

#homepage-screen-3 .homepage-screens-info> .features {
  max-width: 300px;
  align-items: flex-start;
  align-content: flex-start;
  color: var(--textGray);
}

#homepage-screen-3 .homepage-screens-info> .features p {
  margin-top: 0.75rem;
  margin-bottom: 0.75rem;
}

#homepage-screen-3 .homepage-screens-info> .features .features-safety-img-container {
  min-width: 2rem;
  width: 2rem;
  margin-left: 0.5rem;
  margin-right: 1rem;
}

#homepage-screen-3 .homepage-screens-info> .features> .features-safety,
#homepage-screen-3 .homepage-screens-info> .features> .features-edu,
#homepage-screen-3 .homepage-screens-info> .features> .features-time,
#homepage-screen-3 .homepage-screens-info> .features> .features-guaranteed {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
}

#homepage-screen-3 .homepage-screens-info> .facts {
  align-items: center;
  align-content: center;
}

#homepage-screen-3 .homepage-screens-info> .facts> .facts-box-green,
#homepage-screen-3 .homepage-screens-info> .facts> .facts-box-gray {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  width: 100%;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

#homepage-screen-3 .homepage-screens-info> .facts p {
  margin-top: 0rem;
  margin-bottom: 0rem;
  margin-right: 0.5rem;
  color: var(--textGray);
}

#homepage-screen-3 .homepage-screens-info> .facts> .facts-box-green> p:nth-child(2n+1) {
  color: var(--textGreen);
}

#homepage-screen-3 .homepage-screens-info> .facts> .facts-box-gray> p:nth-child(2n+1) {
  color: #bcbcbc;
}

.homepage-screens-info__divider {
  opacity: 0.25;
  width: 3px;
  height: 300px;
  background: linear-gradient(rgba(255,255,255,1) 0%, rgba(109,109,109,1) 51%, rgba(255,255,255,1) 100%);
}

/* END #homepage-screen-3 */

/* START #homepage-screen-4 */

#homepage-screen-4> .homepage-screens-info-wrapper {
  width: 600px;
}

#homepage-screen-4> .homepage-screens-info-wrapper .clients,
#homepage-screen-4> .homepage-screens-info-wrapper .partners {
  width: 600px;
}

#homepage-screen-4 .homepage-screens-info {
  flex-wrap: wrap;
  padding-top: 2rem;
  padding-bottom: 2rem;
  justify-content: space-around;
}

#homepage-screen-4 .homepage-screens-info button {
  display: flex;
  background-color: transparent;
  border: 0px;
  cursor: pointer;
  outline: none;
}

#homepage-screen-4 .homepage-screens-info> .clients> .clients-controls,
#homepage-screen-4 .homepage-screens-info> .partners> .partners-controls {
  display: flex;
  flex-direction: row;
}

.siema-img-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.clients-controls,
.partners-controls {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.clients-controls {
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.partners-controls {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.clients-controls h3,
.partners-controls h3 {
  width: 8rem;
  text-align: center;
  color: var(--textGray);
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 2rem;
  margin-right: 2rem;
}

/* END #homepage-screen-4 */
