/* Фоновые изображения для viewport height < 800px */

@media all and (--mediumHeight) {

  #cleaning-background {
    background-image: url('../img/cleaning-medium@1x.jpg');
  }
  #pest-control-background {
    background-image: url('../img/pest-control-medium@1x.jpg');
  }

}

/* и они же для ретины */

@media all and (--mediumHeight) and (-webkit-min-device-pixel-ratio: 1.5) and (min-resolution: 144dpi) and (min-resolution: 1.5dppx) {

  #cleaning-background {
    background-image: url('../img/cleaning-medium@2x.jpg');
  }
  #pest-control-background {
    background-image: url('../img/pest-control-medium@2x.jpg');
  }

}

/* Фоновые изображения для viewport height > 800px */

@media all and (--largeHeight) {

  #cleaning-background {
    background-image: url('../img/cleaning-large@1x.jpg');
  }
  #pest-control-background {
    background-image: url('../img/pest-control-large@1x.jpg');
  }

}

/* и они же для ретины */

@media all and (--largeHeight) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) and (min-resolution: 2dppx) {

  #cleaning-background {
    background-image: url('../img/cleaning-large@2x.jpg');
  }
  #pest-control-background {
    background-image: url('../img/pest-control-large@2x.jpg');
  }

}

@media (--mobile) {
  #cleaning-background,
  #pest-control-background {
    background-color: white;
    background-image: url();
  }
}

.aboutCleaning,
.aboutPestControl,
.aboutVacancies {
  overflow: hidden;
}

@media (--mobile) {
  .aboutCleaning,
  .aboutPestControl,
  .aboutVacancies {
    overflow: auto;
  }
}

/* Контейнеры для контента */

.cleaning, /* Клининг */
.pest-control, /* Дезинсекция */
.vacancies { /* Вакансии */
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  width: 86%;
  height: 60vh;
  margin-left: auto;
  margin-right: auto;
}

.cleaning-client-select, /* Блок выбора частный/корпоративный клиент */
.vacancies-group-select { /* Блок выбора вакансий клининг/десинсекци */
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  flex-basis: 100%;
  width: 100%;
}

@media (--mobile) {
  .cleaning-client-select {
    justify-content: space-around;
  }
  .cleaning-client-select> button {
    width: 45%;
    font-size: 0.875rem !important;
  }
}

/* Контролы для переключения  */

.cleaning-client-select> button {
  @apply --controls;
  padding: 0;
  margin: 0;
}

.cleaning-client-select> button:hover {
  color: var(--textGray);
}

.cleaning-client-select> button:disabled {
  color: var(--textGreen);
  cursor: default;
}

/* Разделитель */

.cleaning-client-select span {
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  color: var(--textGreen);
}

/* Блоки с информацией для частных/корпоративных клиентов */

.cleaning-personal,
.cleaning-corporate
 {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  width: 100%;
  height: 55vh;
  margin-top: 3vw;
}


@media (--mobile) {
  .cleaning, /* Клининг */
  .pest-control, /* Дезинсекция */
  .vacancies,
  .cleaning-personal,
  .cleaning-corporate {
    height: auto;
  }
}

/* Блоки с информацией об услуге или комлпексе услуг
(переключаются контролами справа) */

.cleaning-description {
  position: relative;
  height: 100%;
  flex-basis: 50%;
  max-width: 735px;
  flex-grow: 1;
  flex-shrink: 1;
  overflow-y: auto;
}

@media (--mobile) {
  .cleaning-description {
    flex-basis: 100%;
    width: 100%;
    order: 2;
    margin-bottom: 2rem;
  }
}

.cleaning-description> h3, /* Заголовок блока с информацией об услуге */
.cleaning-description> p { /* Текст блока */
  width: 95%;
  margin-top: 0;
  margin-left: 0;
  margin-right: 0;
  color: var(--textGray);
}

.cleaning-description> h3 { margin-bottom: 1.5rem; }

.cleaning-description> p { margin-bottom: 1rem; }

/* Контейнер контролов (справа) */

.cleaning-manage {
  height: 90%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  align-content: flex-end;
  flex-basis: 30%;
  flex-grow: 1;
  flex-shrink: 1;
}

@media (--mobile) {
  .cleaning-manage {
    height: auto;
    order: 1;
    margin-bottom: 2rem;
  }
}

/* Блок с переключением по видам услуг */

.service-select {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
  align-content: flex-end;
}

@media (--mobile) {
  .cleaning-order,
  .service-select {
    align-items: flex-start;
    align-content: flex-start;
  }
  .service-select {
    width: 100%;
  }
  .service-select> button {
    text-align: left !important;
    font-size: 1.125rem !important;
  }
}

/* Контролы для переключения по видам услуг */

.service-select> button {
  @apply --controls;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  text-align: right;
}

.service-select> button::before {
  margin-right: 0rem;
  transition-property: margin-right;
  transition-duration: 0.2s;
  transition-timing-function: ease;
}

.service-select> button:disabled::before { /* Стрелка для стилизации */
  background-image: url('../img/selectArrow.svg');
  content: '1';
  background-repeat: no-repeat;
  color: transparent;
  background-position-y: 3px;
  margin-right: 0.5rem;
}

@media (--mobile) {
  .service-select> button {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    align-content: center;
  }
  .service-select> button:disabled::before {
    background-image: url('../img/selectArrow.svg');
    background-size: auto;
    transform: scale(0.75);
  }
}

.service-select> button:hover {
  color: var(--textGray);
}

.service-select> button:disabled {
  color: var(--textGreen);
  cursor: default;
}

/* Внутренние блоки с услугами */

.cleaning-description-service {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.cleaning-description-service> p {
  width: 90%;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  margin-left: 1.1rem;
  margin-right: 0;
  color: var(--textGray);
  border-bottom: 2px;
  border-top: 0px;
  border-left: 0px;
  border-right: 0px;
  border-color: rgba(197,197,197,0.3);
  border-style: solid;
  padding-bottom: 0.5rem;
}

/* Контролы для переключения внутри комплекта услуг */

.cleaning-description-service> button {
  @apply --controls;
  margin: 0;
  padding: 0;
}

.cleaning-description-service> button::before { /* Стрелка для стилизации */
  content: url('../img/selectArrowSmall.svg');
  margin-right: 0.8em;
}

.cleaning-description-service> button:hover {
  color: var(--textGray);
}

#facade h4 {
  color: var(--textGray);
}

/* Расчёт стоимости мойки фасада */

#facadeCalculatePrice {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
}

#facadeCalculatePrice> input,
#facadeCalculatePrice> div {
  width: 13rem;
  max-width: 45%;
  height: 3rem;
  margin-right: 1.5rem;
  margin-bottom: 1.5rem;
  padding-left: 1rem;
  border: 1px;
  border-style: solid;
  border-radius: 3px
}

@media (--mobile) {
  #facadeCalculatePrice {
    justify-content: flex-start;
    align-items: flex-start;
    align-content: flex-start;
  }
  #facadeCalculatePrice> input,
  #facadeCalculatePrice> div {
    width: 85% !important;
    max-width: 85% !important;
    margin-left: 0;
    margin-right: 0;
  }
  #facadeCalculatePrice button {
    width: 90% !important;
    max-width: 90% !important;
  }
}

#facadeCalculatePrice> input {
  border-color: var(--textGray);
  background-color: white;
  transition-property: border-color;
  transition-duration: var(--time);
  transition-timing-function: ease;
  outline: none;
}

#facadeCalculatePrice> input:focus {
  border: 1px;
  border-style: solid;
  border-color: var(--textGray);
  border-radius: 3px;
}

#facadeCalculatePrice> div {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-content: baseline;
  align-items: baseline;
  border-color: white;
  background-color: white;
  color: var(--textGray);
  overflow: auto;
}

#facadeCalculatePrice> button {
  color: var(--textGreen);
  width: 14rem;
  max-width: 45%;
  height: 3rem;
  margin-bottom: 1.5rem;
  padding: 0;
  border: 1px;
  border-style: solid;
  border-color: var(--textGreen);
  border-radius: 3px;
  background-color: color(#c5c5c5 lightness(97%));
  transition-property: background-color;
  transition-duration: var(--time);
  transition-timing-function: ease;
  outline: none;
  cursor: pointer;
}

#facadeCalculatePrice> button> .bold16 {
  margin-top: 2px;
  margin-bottom: 0px;
}

#facadeCalculatePrice> button:hover {
  background-color: color(#c5c5c5 lightness(99%));
}

#facadeCalculatePrice> button:active {
  background-color: color(#c5c5c5 lightness(97%));
}

#facadePrice> p {
  margin-top: 0.6rem;
  margin-bottom: 0;
  margin-right: 0.5rem;
  margin-left: 0;
  line-height: normal;
  color: var(--textGray);
  font-size: 0.85rem;
}

#facadePrice> span {
  margin-top: 0.3rem;
  margin-left: 6px;
  margin-right: 0px;
}

/* Контейнер кнопки "Рассчитать стоимость" */

.cleaning-order {
  position: absolute;
  bottom: 0;
  right: 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  align-content: center;
  width: 40%;
}

.phone-number-mobile {
  display: none;
}

@media (--mobile) {
  .cleaning-order {
    order: 3;
    position: relative;
    width: 100%;
    justify-content: space-between;
    margin-bottom: 2rem;
  }
  .phone-number-mobile {
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    font-size: 25px;
    color: var(--textGray);
    width: 80%;
    margin-bottom: 2rem;
  }
}

/* Ссылка на примеры работ */

.cleaning-order> a {
  text-decoration: underline;
  text-align: center;
  margin-right: 1.5rem;
}

/* Кнопка "Рассчитать стоимость" */

.cleaning-order> button {
  background-color: transparent;
  color: #b5c832;
  border: 1.5px;
  border-style: solid;
  border-radius: 3px;
  border-color: #b5c832;
  cursor: pointer;
  padding-left: 1.75rem;
  padding-right: 1.75rem;
  padding-top: 0.75rem;
  padding-bottom: 0.60rem;
  outline: none;
  transition-property: background-color;
  transition-duration: var(--time);
  transition-timing-function: ease;
}

.cleaning-order> button:hover {
  background-color: white;
}

.cleaning-order> button:active {
  background-color: color(black alpha(2%));
}
