:root {
  --light: 'Lato Light';
  --regular: 'Lato Regular';
  --semibold: 'Lato Semibold';
  --bold: 'Lato Bold';
  --italic: 'Lato Italic';
  --textGray: #6d6d6d;
  --textGreen: #b5c832;
  --time: 0.2s;
  --controls: {
      background-color: transparent;
      color: #c5c5c5;
      border: 0px;
      cursor: pointer;
      outline: none;
      transition-property: color;
      transition-duration: 0.2s;
      transition-timing-function: ease;
  };
  --customButton: {
    background-color: white;
    border-radius: 3px;
    border: 1px;
    border-color: var(--textGreen);
    color: var(--textGreen);
    border-style: solid;
    transition-property: background-color;
    transition-duration: 0.2s;
    transition-timing-function: ease;
    outline: none;
    cursor: pointer;
    padding-top: 0.7rem;
    padding-bottom: 0.65rem;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  };
}

@custom-media --mediumHeight (height <= 750px);
@custom-media --largeHeight (height > 750px);

@custom-media --mobile (max-width: 600px);
@custom-media --small (width > 600px) and (width <= 900px);
@custom-media --medium (width >= 900px) and (width <= 1200px);
@custom-media --large (width >= 1200px) and (width <= 1600px);
@custom-media --xlarge (width >= 1600px);
