#contacts-page {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
  left: 7%;
  width: 86%;
}

@media (--mobile) {
  #contacts-page {
    flex-direction: column;
    width: 100%;
    left: 0;
    height: auto;
    align-items: flex-start;
    align-content: flex-start;
  }
}

.contacts {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: flex-start;
  align-items: flex-start;
  width: 30%;
}

.contacts> h2 {
  color: var(--textGreen);
}

.contacts__phone {
  color: var(--textGray);
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.contacts__phone> .underline {
  margin-top: 1px;
}

.contacts__adress {
  color: var(--textGray);
  margin-top: 2rem;
  margin-bottom: 4rem;
}

.contacts> .social-buttons> a {
  margin-right: 0.6rem;
  margin-left: 0.6rem;
}

.contacts> .social-buttons> a:first-child {
  margin-left: 0;
}

.contacts> .social-buttons> a:last-child {
  margin-right: 0;
}

.map {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: flex-start;
  align-items: flex-start;
  width: 60%;
  height: 55vh;
}

.footer-mobile {
  display: none;
}

@media (--mobile) {
  .contacts,
  .footer-mobile {
    width: 86%;
    left: 7%;
    position: relative;
  }
  .contacts> h2 {
    margin-top: 0;
  }
  .contacts__phone {
    font-size: 1.25rem;
  }
  .contacts__adress {
    margin-top: 0.5rem;
    margin-bottom: 1rem;
  }
  .map {
    width: 100%;
    height: 30vh;
  }
  .social-buttons {
    display: none;
  }
  .footer-mobile {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    align-content: center;
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
  .social-buttons-mobile,
  .copyright-mobile {
    display: inline-block;
    margin-top: 0.25rem;
    margin-bottom: 0.5rem;
  }
}
